<template>
 <v-layout fill-height column class="header__frame">
  <div class="d-flex justify-space-between align-end">
    <v-app-bar-nav-icon @click.stop="$emit('menuClick')">
      <i 
          class="fas fa-bars fa-2x menuBar" 
          style="color: #b7b7b7;"
      />
    </v-app-bar-nav-icon>
    <div class="d-flex align-center items__frame" >
      <div 
          v-for="item,index in items"
          :key="item.path"
          class="items"
          :class="[hoverIndex!=null && hoverIndex != index ? 'items__font--dark' : '',findIndex == index ? 'items__font--hover' : '']"
          @click.stop="pushRouter({path : item.path})"
          @mouseover="hoverIndex = index"
          @mouseleave="hoverIndex = null"
        >
          <div 
            class="items__font" 
          >
            {{ item.title }}
          </div>
        </div>
    </div>
  </div>
 </v-layout>
</template>

<script>

export default {
  components: {
  },
  props: {},
  data() {
      return {
        hoverIndex:null,
        items:[
          {
            title:'Home',
            icon:'house',
            path:'/',
          },
          {
            title:'About me',
            icon:'user',
            path:'/about',
          },
          {
            title:'Skills',
            icon:'fire',
            path:'/skills',
          },
          {
            title:'Portfolio',
            icon:'book',
            path:'/portfolio',
          },
          {
            title:'Contact',
            icon:'hand-peace',
            path:'/contact',
          },
        ],
      };
  },
  watch: {
  },
  computed:{
    findIndex(){
      return this.items.findIndex(obj => obj.path.includes(this.$route.path))
    }
  },
  created() {},
  mounted() {
  },
  destroyed() {},
  methods: {
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/app.scss";
.header__frame{
  max-width: 80%;
  margin: 0 auto;
  padding: 12px 0px;
}
.menuBar{
  cursor: pointer;
}
.items{
  border-bottom: 1px solid #505050;
  &::after {
    content: '';
    display: block;
    width: 0%;
    height: 2px;
    background: #fff;
    transition: width 1s;
  }
  &:hover::after {
      background: #fff;
      width: 100%;
  }
  &__font{
    font-size: 20px;
    color: #f5f5f5;
    cursor: pointer;
    &--dark{
      opacity: 0.4;
      transition: 1s;
    }
    &--hover{
      &::after {
        content: '';
        display: block;
        width: 100%;
        height: 2px;
        background: #fff;
      }
    }
  }
  &__frame{
    justify-content: space-between;
    width: 500px;
  }
}
</style>