<template>
 <div >
  <div 
    v-for="item,index in items"
    :key="item.path"
    class="items"
    :style="{
      color : hoverIndex == index || findIndex == index ? item.color : ''
    }"
    @click.stop="pushRouter({path : item.path})"
    @mouseover="hoverIndex = index"
    @mouseleave="hoverIndex = null"
    v-ripple
  >
    <i 
      :class="[`fas fa-${item.icon} fa-2x`]" 
    />
    <div 
      class="items__font" 
      :class="hoverIndex == index || findIndex == index ? 'items__font--hover' : ''"
    >
      {{ item.title }}
    </div>
  </div>
 </div>
</template>

<script>

export default {
  components: {
  },
  
  props: {},
  data() {
      return {
        hoverIndex:null,
        items:[
          {
            title:'Intro',
            icon:'house',
            path:'/',
            color:'#16FF00'
          },
          {
            title:'About me',
            icon:'user',
            path:'/about',
            color:'#537FE7'
          },
          {
            title:'Skills',
            icon:'fire',
            path:'/skills',
            color:'red'
          },
          {
            title:'Portfolio',
            icon:'book',
            path:'/portfolio',
            color:'#AA5656'
          },
          {
            title:'Contact',
            icon:'hand-peace',
            path:'/contact',
            color:'#FF8B13'
          },
        ],
        
      };
  },
  computed:{
    findIndex(){
      return this.items.findIndex(obj => obj.path.includes(this.$route.path))
    }
  },
  watch: {
  },
  created() {},
  mounted() {
  },
  destroyed() {},
  methods: {
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/app.scss";

.items{
  cursor: pointer;
  display: flex;
  align-items: center;
  margin: 25px 20px 10px 20px;
  padding: 5px;

  &__font{
    font-size: 35px;
    padding-left: 20px;
    &--hover{
      font-weight: 700;
    }
  }
}
a {
  text-decoration: none;
}
</style>